@font-face {
    font-family: 'icomoon';
    src:  url('../assets/fonts/icomoon.eot?5iswfo');
    src:  url('../assets/fonts/icomoon.eot?5iswfo#iefix') format('embedded-opentype'),
      url('../assets/fonts/icomoon.ttf?5iswfo') format('truetype'),
      url('../assets/fonts/icomoon.woff?5iswfo') format('woff'),
      url('../assets/fonts/icomoon.svg?5iswfo#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
  }
  
  [class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
  
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  .icon-Cross:before {
    content: "\e900";
  }
  .icon-Hamburger:before {
    content: "\e901";
  }
  